import BScroll from 'better-scroll'

import request from '../utils/request'

import noData from '../components/noData'
import loading from '../components/loading'
export default {
  name: 'news',
  components: { noData, loading },
  data () {
    return {
      newsScroll: null,
      loading: false,
      newsList: [],
      page: 0,
      label: '',
      menuNav: [{
        text: '推荐', id: '0'
      }, {
        text: '高考', id: '1'
      }, {
        text: '志愿', id: '2'
      }, {
        text: '心理', id: '3'
      }]
    }
  },
  computed: {
    noNews () {
      return this.newsList.length === 0
    },
    teaOpenid () {
      return this.$store.state.user.infoObj.teaOpenid || ''
    },
    stuOpenid () {
      return this.$store.state.user.infoObj.stuOpenid || ''
    },
    openid () {
      return this.teaOpenid || this.stuOpenid
    }
  },
  mounted () {
    this.page = 0
    this.label = this.menuNav[0].text
    this.getList()
  },
  methods: {
    _initScroll () {
      this.newsScroll = new BScroll(this.$refs.newsWrapper, {
        click: true,
        pullDownRefresh: {
          threshold: 50,
          stop: 40
        },
        pullUpLoad: {
          threshold: 50,
          moreTxt: '加载中',
          noMoreTxt: '没有更多数据了'
        }
      })
      this.newsScroll.on('pullingUp', () => {
        console.log('finishPullUp')
        this.page++
        new Promise((resolve) => {
          this.getList(resolve)
        }).then((newData) => {
          this.newsScroll.finishPullUp()
        })
      })
      this.newsScroll.on('pullingDown', () => {
        this.page = 0
        console.log('pullingDown')
        new Promise((resolve) => {
          this.getList(resolve)
        }).then((newData) => {
          this.newsScroll.finishPullDown()
        })
      })
    },
    changeLabel (item) {
      this.label = item.text
      this.page = 0
      this.newsScroll.scrollTo(0, 0, 500)
      this.newsList = []
      this.getList()
    },
    getList (resolve) {
      if (this.page === 0) {
        this.loading = true
      }
      request({
        url: 'http://www.zhiheyikaoqin.cn/sdc/article/getArticleList',
        method: 'post',
        data: {
          openid: this.openid,
          page: this.page,
          pageSize: 10,
          label: this.label
        }
      }).then(res => {
        if (resolve !== undefined) {
          resolve()
        }
        if (this.newsList.length === 0) {
          this.newsList = res.data.data.map(item => {
            return item
          })
        } else {
          this.newsList = this.newsList.concat(res.data.data.map(item => {
            return item
          }))
        }
        this.loading = false
        this.$nextTick(() => {
          this._initScroll()
        })
      })
    },
    jumpToDetail (id) {
      this.$router.push({ path: '/news/' + id })
    }
  }
}



